import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { withStyles } from '@material-ui/core/styles';
import red from '@material-ui/core/colors/red';
import Icon from '@material-ui/core/Icon';
import IconButton from '@material-ui/core/IconButton';
import FavoriteBorder from '@material-ui/icons/FavoriteBorder';
import Responsive from 'react-responsive';
import Button from '@material-ui/core/Button';
import PictureAsPdfIcon from '@material-ui/icons/PictureAsPdf';
import * as path from "path";


const Desktop = props => <Responsive {...props} minWidth={660} />;
const Mobile = props => <Responsive {...props} maxWidth={660} />;

const styles = theme => ({
  root: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'flex-end',
  },
  icon: {
    margin: theme.spacing.unit/3,
  },
  copyright: {
    marginTop: 0,
  }
});

const heartStyle = {
  position: 'relative',
  top: '6px'
};

class Footer extends React.Component {
  render() {
    const { classes } = this.props;

    return (
      <div>
        <div className={classes.root}>
          <Button variant="contained" size="large" color="primary" href={path.join(process.env.PUBLIC_URL, "resources", "Conlon_Novak_Resume.pdf")}>
              Resume
          <PictureAsPdfIcon className={classes.icon} />
          </Button>
          <Desktop>
            <Button size="large" color="primary" href="https://github.com/conlonnovak">
              Github
              <Icon className={classNames(classes.icon, 'fab fa-github fa-inverse fa-sm')} color="primary"/>
            </Button>
            <Button size="large" color="primary" href="https://devpost.com/conlonnovak">
              Devpost
              <Icon className={classNames(classes.icon, 'fab fa-dev fa-inverse fa-lg')} color="primary"/>
            </Button>
          </Desktop>
          <Mobile>
            <IconButton className={classNames(classes.icon, 'fab fa-github fa-inverse fa-lg')} color="primary" href="https://github.com/conlonnovak"/>
            <IconButton className={classNames(classes.icon, 'fab fa-dev fa-inverse fa-lg')} color="primary" href="https://devpost.com/conlonnovak"/>
          </Mobile>
          <IconButton className={classNames(classes.icon, 'fab fa-linkedin-in fa-inverse fa-lg')} color="primary" href="https://www.linkedin.com/in/conlon-novak/"/>
          <Desktop>
            <IconButton className={classNames(classes.icon, 'fab fa-facebook-f fa-inverse fa-lg')} color="primary" href="https://www.facebook.com/Conlon.Novak"/>
            <IconButton className={classNames(classes.icon, 'fab fa-twitter fa-inverse fa-lg')} color="primary" href="https://twitter.com/Conlon_Novak"/>
            <IconButton className={classNames(classes.icon, 'fab fa-instagram fa-inverse fa-lg')} color="primary" href="https://instagram.com/conlonnovak/"/>
          </Desktop>
          <IconButton className={classNames(classes.icon, 'fas fa-envelope fa-inverse fa-lg')} color="primary" href="mailto:conlonnovak+website@cmu.edu"/>
        </div>
        <div className={classes.copyright}>
          <p>&#169; {(new Date()).getFullYear()} <a href="mailto:conlonnovak+website@cmu.edu">Conlon Novak</a>. All rights reserved. <Mobile><br/></Mobile>Made with <FavoriteBorder color="secondary" style={heartStyle}/> using <a href="https://reactjs.org/">React</a> and <a href="https://material-ui.com/">Material-UI</a>.</p> 
        </div>
      </div>
    );
  }
}

Footer.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Footer);