import React, { Component } from "react";
import * as path from "path";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Chip from '@material-ui/core/Chip';
import classNames from 'classnames';
import Button from '@material-ui/core/Button';
// import Popover from '@material-ui/core/Popover';
// import Badge from '@material-ui/core/Badge';
import "./App.css";
import AwardsPopoverButton from './AwardsPopoverButton.js';
import Tooltip from '@material-ui/core/Tooltip';
import Responsive from 'react-responsive';
import SchoolIcon from '@material-ui/icons/School';
import WorkIcon from '@material-ui/icons/Work';
import PeopleIcon from '@material-ui/icons/People';

import CardActionArea from '@material-ui/core/CardActionArea';
import Icon from '@material-ui/core/Icon';
import PictureAsPdfIcon from '@material-ui/icons/PictureAsPdf';
import SendIcon from '@material-ui/icons/Send';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Paper from '@material-ui/core/Paper';
import PhoneIcon from '@material-ui/icons/Phone';
import FavoriteIcon from '@material-ui/icons/Favorite';
import PersonPinIcon from '@material-ui/icons/PersonPin';

const Desktop = props => <Responsive {...props} minWidth={660} />;
const Mobile = props => <Responsive {...props} maxWidth={660} />;

const styles = theme => ({
  card: {
    maxWidth: 700,
    width: '95%',
    padding: 10,
    margin: 'auto',
    marginBottom: 10,
    marginTop: 10,
  },
  media: {
    height: 200,
    paddingTop: '56.25%', // 16:9
  },
  actions: {
    display: 'flex',
  },
  content: {
    textAlign: 'left',
  },
  expand: {
    transform: 'rotate(0deg)',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
    marginLeft: 'auto',
    [theme.breakpoints.up('sm')]: {
      marginRight: -8,
    },
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  chip: {
    padding: theme.spacing.unit / 2,
    margin: theme.spacing.unit / 2,
  }
});


class AboutBlurbs extends Component {
  renderAboutBlurbs() {
    const { classes } = this.props;
    return (
      <div>
        <div className="row">
          <Card className={classes.card}>
            <CardContent>
              <Typography variant="h4">
                Blazing an Interdisiplinary Path
              </Typography>
              <Typography align="left" paragraph>
              <br/>
              At CMU, it’s difficult to feel like you’re really breaking new ground. Beyond a select few opportunities, you’re generally taking classes that thousands of others have taken before you, in a program that’s been similarly well-tread, at a school that, while the top of its class in many respects, is still fighting its own institutional inertia to better meet the future. With that weighing on my mind, I set out to find my passion here - taking a variety of courses from across the different colleges, forming meaningful relationships with teachers and graduate students doing interesting research, and diving headlong to the world beyond academics. And that’s what lead me here, to the crossroads of my passions—working with users to solve the right problems in the right ways.
              <br/>
              <br/>
              Studying Information Systems and Human-Computer Interaction at Carnegie Mellon University has allowed me to appreciate the consulting process from multiple distinct perspectives, and to approach challenges from multiple distinct perspectives. The necessity of this understanding lies at the heart of any collaborative work, especially when implemented will need to meet both the underlying as well as the organizational needs of the client and may need to be maintained for years to come. My extensive technical experience in web development (HTML, CSS, JS, React, and Vue.js), application design (Ruby on Rails), and mobile development (Android), in addition to the user research and design experience I’ve gained through HCII make me an ideal candidate to design and deploy user-centric solutions across a variety of platforms and technologies. My experience with user research, software development, and design afford me the opportunity to work at the intersection of my passions – solving interesting technical challenges that offer the opportunity to meaningfully improve user experiences on a daily basis.<br/><br/>
              Looking ahead, I'm seeking personal and professional growth throughout my career. In the near future, I hope to continuously make significant, measurable contributions to my organization, meaningfully improving an aspect of their work, thus improving their productivity, efficiency, and contributing to a larger culture of collaboration and innovation.
              </Typography>
            </CardContent>
          </Card>
        </div>
        <div className="row">
          <Card className={classes.card}>
            <Typography gutterTop variant="h4">
              Work and Education History
            </Typography>
          <div className="row">
            <Card className={classes.card}>
              <CardContent>
                <SchoolIcon className={classes.icon} color='primary'/>
                <Typography variant="h5">
                  <a href='https://www.cmu.edu/'>Carnegie Mellon University</a>
                </Typography>
                <Typography variant="subtitle1">
                  <Desktop>B.S. <a href='https://www.cmu.edu/information-systems/'>Information Systems</a> and <a href='https://hcii.cmu.edu/'>Human-Computer Interaction</a><br/></Desktop>
                  <Mobile>B.S. <a href='https://www.cmu.edu/information-systems/'>Information Systems</a>,<br/><a href='https://hcii.cmu.edu/'>Human-Computer Interaction</a><br/></Mobile>
                  Minor in <a href='https://www.cmu.edu/dietrich/english/undergraduate/dietrich-college-interdepartmental-minor/film-media-studies.html'>Film and Media Studies</a><br/>
                  3.60/4.0 GPA<br/>
                  Class of 2020
                </Typography>
              </CardContent>
            </Card>
          </div>
          <div className="row">
            <Card className={classes.card}>
              <CardContent>
                <SchoolIcon className={classes.icon} color='primary'/>
                <Typography variant="h5">
                  <a href='https://www.cmu.edu/'>Carnegie Mellon University</a>
                </Typography>
                <Typography variant="subtitle1">
                  <Desktop>B.S. <a href='https://www.cmu.edu/information-systems/'>Information Systems</a> and <a href='https://hcii.cmu.edu/'>Human-Computer Interaction</a><br/></Desktop>
                  <Mobile>B.S. <a href='https://www.cmu.edu/information-systems/'>Information Systems</a>,<br/><a href='https://hcii.cmu.edu/'>Human-Computer Interaction</a><br/></Mobile>
                  Minor in <a href='https://www.cmu.edu/dietrich/english/undergraduate/dietrich-college-interdepartmental-minor/film-media-studies.html'>Film and Media Studies</a><br/>
                  3.60/4.0 GPA<br/>
                  Class of 2020
                </Typography>
              </CardContent>
            </Card>
          </div>
          <div className="row">
            <Card className={classes.card}>
              <CardContent>
                <WorkIcon className={classes.icon} color='primary'/>
                <Typography variant="h5">
                  <a href='http://www.lutron.com'>Lutron Electronics Company</a>
                </Typography>
                <Typography variant="subtitle1">
                  Information Technology Co-op Intern<br/>
                  Summer 2019
                </Typography>
              </CardContent>
            </Card>
          </div>
          <div className="row">
            <Card className={classes.card}>
              <CardContent>
                <PeopleIcon className={classes.icon} color='primary'/>
                <Typography variant="h5">
                  <a href='https://www.cmu.edu/student-affairs/orientation/index.html'>CMU Office of First-Year Orientation</a>
                </Typography>
                <Typography variant="subtitle1">
                  Head Orientation Counselor, <a href='https://cmudonner.weebly.com/'>Donner House</a> (since 2019)<br/>
                  2017 - Present
                </Typography>
              </CardContent>
            </Card>
          </div>
          <div className="row">
            <Card className={classes.card}>
              <CardContent>
                <WorkIcon className={classes.icon} color='primary'/>
                <Typography variant="h5">
                  <a href='https://sei.cmu.edu/'>Software Engineering Institute</a>
                </Typography>
                <Typography variant="subtitle1">
                  Technical Intern, <a href='https://www.sei.cmu.edu/about/divisions/software-solutions-division/index.cfm'>Tactical Technologies Group</a><br/>
                  Summer 2017, 2018
                </Typography>
              </CardContent>
            </Card>
          </div>
          <div className="row">
            <Card className={classes.card}>
              <CardContent>
                <WorkIcon className={classes.icon} color='primary'/>
                <Typography variant="h5">
                  <a href='http://hcii.cs.cmu.edu/'>Human-Computer Interaction Institute</a>
                </Typography>
                <Typography variant="subtitle1">
                  Lead Science Annoatation Intern, <a href='http://articulab.hcii.cs.cmu.edu/'>The ArticuLab</a><br/>
                  October 2016 - May 2017
                </Typography>
              </CardContent>
            </Card>
        </div>
      </Card>
    </div>
    </div>
    );
  }

render() {
  return (
    <div>
      {this.renderAboutBlurbs()}
    </div>
    )
  }
}

export default withStyles(styles)(AboutBlurbs);