import React from 'react';
import PropTypes from 'prop-types';
import Paper from '@material-ui/core/Paper';
import { withStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import FormatShapes from '@material-ui/icons/FormatShapes';
import DevicesOtherIcon from '@material-ui/icons/DevicesOther';
import PersonIcon from '@material-ui/icons/Person';
import ProjectList from './ProjectList.js';
import DesignProjectList from './DesignProjectList.js';
import AboutBlurbs from './AboutBlurbs.js';

const styles = {
  root: {
    flexGrow: 1,
    maxWidth: 700,
    width: '95%',
    margin: 'auto',
  },
};

class ContentTabs extends React.Component {
  state = {
    value: 1,
  };

  handleChange = (event, value) => {
    this.setState({ value });
  };

  renderContent() {

    if (this.state.value == 0)
      return(<AboutBlurbs />)
    else if (this.state.value == 1)
      return(<ProjectList />)
    else
      return(<DesignProjectList />)
  }

  renderTabs() {
    const { classes } = this.props;

    return (
      <div>
        <Paper square className={classes.root}>
          <Tabs
            value={this.state.value}
            onChange={this.handleChange}
            indicatorColor='primary'
            textColor='primary'
            centered
          >
            <Tab icon={<PersonIcon />} label="ABOUT" />
            <Tab icon={<DevicesOtherIcon />} label="DEVELOPMENT" />
            <Tab icon={<FormatShapes />} label="DESIGN" />
          </Tabs>
        </Paper>
        {this.renderContent()}
      </div>
    );
  }

  render() {
    return (
      <div>
        {this.renderTabs()}
      </div>
    )
  }
}

ContentTabs.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ContentTabs);