import React, { Component } from "react";
import * as path from "path";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import classNames from 'classnames';
import Icon from '@material-ui/core/Icon';
import PictureAsPdfIcon from '@material-ui/icons/PictureAsPdf';
import SendIcon from '@material-ui/icons/Send';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import WorkIcon from '@material-ui/icons/Work';
import Responsive from 'react-responsive';
import IconButton from '@material-ui/core/IconButton';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Paper from '@material-ui/core/Paper';
import PhoneIcon from '@material-ui/icons/Phone';
import FavoriteIcon from '@material-ui/icons/Favorite';
import PersonPinIcon from '@material-ui/icons/PersonPin';

const Desktop = props => <Responsive {...props} minWidth={660} />;
const Mobile = props => <Responsive {...props} maxWidth={660} />;

const styles = {
  root: {
    flexGrow: 1,
    maxWidth: 500,
  },
  card: {
    maxWidth: 700,
    width: '95%',
    padding: 10,
    margin: 'auto',
    marginBottom: 10,
    marginTop: 10,
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  icon:{
    marginLeft: 10,
  },
  actions:{
    display: 'block',
    textAlign: 'center',
    alignItems: 'center',
    justifyItems: 'center',
    margin: 'auto',
  },
  media:{
    height: '30%',
    width: '30%',
    borderRadius: '50%',
    display:'block',
    margin:'auto',
  }
};

class About extends Component {

  state = {
    value: 0,
  };

  handleChange = (event, value) => {
    this.setState({ value });
  };

  renderAbout() {
    const { classes } = this.props;
    return (
      <Card className={classes.card}>
        <CardMedia
          component="img"
          alt="Conlon Novak"
          className={classes.media}
          image={path.join(process.env.PUBLIC_URL, "resources", "portrait.jpg")}
          title="Conlon Novak"
        />
        <CardContent>
          <Desktop>
            <Typography gutterBottom variant="h2">
              Conlon Novak
            </Typography>
          </Desktop>
          <Mobile>
            <Typography gutterBottom variant="h4">
              Conlon Novak
            </Typography>
          </Mobile>
          <Typography variant="h5">
            Consulting at the Intersection of
          </Typography>
          <Typography variant="h4">
            Development and Design
          </Typography>
        </CardContent>
        <CardActions className={classes.actions} disableActionSpacing>
          <Button variant="contained" size="large" color="primary" href={path.join(process.env.PUBLIC_URL, "resources", "Conlon_Novak_Resume.pdf")}>
              Resume
            <PictureAsPdfIcon className={classes.icon} />
          </Button>
        </CardActions>
      </Card>
    );
  }

  render() {
    return (
      <div>
        {this.renderAbout()}
      </div>
    )
  }
}

About.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(About);